\r\n\r\n.sidebar ul {\r\n    margin-top: 20px;\r\n    padding-left: 0;\r\n}\r\n\r\n.sidebar ul li {\r\n    position: relative;\r\n    height: 50px;\r\n    width: 100%;\r\n    list-style: none;\r\n    line-height: 20px;\r\n    border-radius: 12px;\r\n}\r\n\r\n.sidebar .tooltip {\r\n    position: absolute;\r\n    left: 122px;\r\n    top: 0%;\r\n    transform: translateY(-50%, -50%);\r\n    border-radius: 6px;\r\n    height: 35px;\r\n    width: 170px;\r\n    background: #fff;\r\n    font-size: 15px;\r\n    line-height: 35px;\r\n    text-align: center;\r\n    white-space: nowrap;\r\n    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);\r\n    transition: 0s;\r\n    opacity: 0;\r\n    pointer-events: none;\r\n    display: block;\r\n}\r\n\r\n.sidebar ul li:hover .tooltip {\r\n    transition: all 0.5s ease;\r\n    opacity: 1;\r\n    top: 50%;\r\n}\r\n\r\n.sidebar .profile_content .profile:hover .tooltip {\r\n    color: #11101d;\r\n    background: #fff;\r\n    transition: all 0.5s ease;\r\n    top: 10%;\r\n    opacity: 1;\r\n}\r\n\r\n\r\n.sidebar-active .tooltip {\r\n    display: none;\r\n}\r\n\r\n.sidebar ul li input {\r\n    position: absolute;\r\n    height: 100%;\r\n    width: 100%;\r\n    left: 0;\r\n    top: 0;\r\n    border-radius: 12px;\r\n    outline: none;\r\n    border: none;\r\n    background: #1d1b31;\r\n    padding-left: 50px;\r\n    font-size: 18px;\r\n    color: #fff;\r\n}\r\n\r\n.sidebar ul li .bx-search {\r\n    position: absolute;\r\n    color: #fff;\r\n    font-size: 22px;\r\n    transition: all 0.5s ease;\r\n    z-index: 99;\r\n}\r\n\r\n.sidebar ul li .bx-search:hover {\r\n    background: #fff;\r\n    color: #1d1b31;\r\n}\r\n\r\n.sidebar ul li a {\r\n    color: #fff;\r\n    display: flex;\r\n    align-items: center;\r\n    text-decoration: none;\r\n    transition: all 0.4s ease;\r\n    border-radius: 12px;\r\n    white-space: nowrap;\r\n}\r\n\r\n.sidebar ul li a:hover {\r\n    color: #11101d;\r\n    background: #fff;\r\n}\r\n\r\n.sidebar ul li i {\r\n    heigh